:root {
  --main-text-color: hsla(230, 10%, 30%, 1);
}
button,select,input,option,textarea{
  font-family: "Inter",sans-serif !important;
}
select {
  background-color: -internal-light-dark(rgb(255, 255, 255), rgb(59, 59, 59));
}

.main{
   max-width: 64rem;
        max-height: 49rem;
        margin: auto;
        border: 0px !important;
        border-radius: 5px;
      /* overflow: hidden; */
    
}
.main .rad-main-box {
  height: calc(14.16667rem + 35vw);
  min-height: 46.75rem;
  /* margin-top: 2rem; */
}
.main-box{
  overflow-y: auto !important; 
  max-width: 100%;
  min-height: 100%;
  display: flex;
}
.left-sec{
    background: #eff2f6 url(/SignIn_Work_04/static/media/modal-bottom.b7365c4c.png) no-repeat bottom;
    background-size: contain;
    width: 40%;
    border: 0;
}
.left-sec > img {
    padding: 2.5rem;
    padding-bottom: 0px;
    width: 150px;
}
.left-content {
    padding: 2.5rem;
    padding-bottom: 0px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    height: 60%;
  }
  .left-content-welcome {
    font-size: 1.5rem;
    line-height: 1.875rem;
    font-weight: 600;
  }
  .left-content-signin {
    margin-top: 0.7rem;
    color: #788699;    
    font-size: .875rem;
    line-height: 1.25rem;
    font-weight: 400;
  }
  .right-sec{
      background-color: #fff;
      width: 63%;
      overflow-y: auto;
      padding: 1.5rem 5rem;
      position: static;
  }
  .right-sec > .MuiSvgIcon-root{
    position: absolute;
    font-size: 35px !important;
    color: #999 !important;
    font-weight: 100 !important;
    cursor: pointer;
    transition: all 0.2s ease-in;
    border-radius: 50px;
    right: 1%;
    top: 1.5%;
  }
  .right-sec > .MuiSvgIcon-root:hover {
    color: #000 !important;
    background-color: lightgray;
  }
.r-s-google{
    display: flex;
    justify-content: center;
    align-items: center;
    background: #fff;
    color:  #1a73e8;
    border: 1px solid #1a73e8;
    font-size: .875rem;
    font-weight: 600;
    line-height: 1.25rem;
    padding: .625rem .75rem;
    cursor: pointer;
    border-radius: 3px;
    margin-top: 3%;
}
.r-s-google > p{
    margin: 0px;
    padding: 0px;
}
.r-s-google > img {
    height: 15px;
    margin-right: 10px;
}
 .sec-seperator {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    color: #141820;
    margin: 1.1625rem 0 .9375rem 0;
  }
  .sec-seperator > p {
      font-size: 18px;
      margin-left: 10px;
      margin-right: 10px;
      margin-top: 0.8em;
  }
  .sec-seperator > hr {
      height: 1.2px;
      background-color: #dfe3eb ;
      border: none;
      width: 50%;
  }
  .right-form{

  }
  .right-form-body .r-f-b-button{
    margin-top: 1rem;
    min-width: 9rem;
    min-height: auto;
    font-size: 0.875rem;
   }
   .right-form-body .disabled{
    margin-top: 1rem;
    min-width: 9rem;
    min-height: auto;
    font-size: 0.875rem;
   }
   .right-form-body > label {
    font-size: .875rem;
    color: #201e27;
    margin: 0 0 2%;
    margin-top: .625rem;
    font-weight: 600;
    display: block;
    line-height: 1.6;
    max-width: 42em;
   }
   input {
    -webkit-writing-mode : horizontal-tb !important;
    text-rendering: auto;
    color: -internal-light-dark(black, white);
    letter-spacing: normal;
    word-spacing: normal;
    text-transform: none;
    text-indent: 0px;
    text-shadow: none;
    display: inline-block;
    text-align: start;
    -webkit-appearance: textfield;
       -moz-appearance: textfield;
            appearance: textfield;
    background-color: -internal-light-dark(rgb(255, 255, 255), rgb(59, 59, 59));
    -webkit-rtl-ordering: logical;
    cursor: text;
    margin: 0em;
    font: 400 13.3333px Arial;
   }
   .right-form-body > input  {
    border-radius: .3125rem;
    margin-bottom: 0.6rem;
    width: 96%;
    font-size: .875rem;
    font-family: "Inter",sans-serif !important;
    padding: 10px;
    outline: none;
    caret-color: #48ccac; 
    border: 1px solid #ddd;
    transition: all 0.3s ease-in;
}
.r-f-b-button {
    margin: auto;
    margin-top: 1rem;
    margin-bottom: 3rem;
    text-align: center;
    width: 100%;
    color: #fff;
    background: #1a73e8;
    padding: .625rem .75rem;
    cursor: pointer;
    border-radius: 5px;
    max-width: 130px;
    outline: none;
    border: none;
    display: block;
    text-align: center;
    transition: all 0.2s ease-in;
    font-weight: 700;
    line-height: 1.375rem;
}

.disabled {
  margin: auto;
  margin-top: 1rem;
  margin-bottom: 3rem;
  text-align: center;
  width: 100%;
  color: #fff;
  background: #1a73e8;
  padding: .625rem .75rem;
  cursor: pointer;
  border-radius: 5px;
  max-width: 130px;
  outline: none;
  border: none;
  display: block;
  text-align: center;
  transition: all 0.2s ease-in;
  font-weight: 700;
  line-height: 1.375rem;
}
.disabled {
    opacity: 0.7;
}
.form-errors {
    color: #e0133e;
    width: 100%;
    margin-top: 0.01em;
    font-weight: bold;
    font-size: .8rem;
    display: flex;
    align-items: center;
}
.form-errors > .MuiSvgIcon-root {
    color: #e0133e !important;
    font-size: 1.1rem !important;
}

